import popupMe from "@/components/popupMe-again.vue";
import chanpinyaosu from "@/components/inquiry/chanpinyaosu-quote.vue";
import baojiaDetail from "@/components/inquiry/baojia-detail-buy.vue";
import sellerInfo from "@/components/inquiry/sellerInfo.vue";

let _this,_quotationNumber
export default {
	components:{
		chanpinyaosu,
		baojiaDetail,
		sellerInfo,
		popupMe
		
	},
	data() {
		return {
			quotationInfo:''
		};
	},
    onLoad:function(options) {
		_this = this
    	_quotationNumber = options.quoteNumber
		console.log(_quotationNumber)
		this.getInquiryInfo()
    },
	methods:{
		submit:function(){
			this.$refs.unMatchRef.show()
			// uni.navigateTo({
			// 	url: './startQuote?inquiryNumber='+ _this.inquiryNumber,
			// 	success: res => {},
			// 	fail: () => {},
			// 	complete: () => {}
			// });
			
		},
		getContent:function(label,content){
					switch (label){
						case 'unMatchRef':
							 let price= content[0]
							 let remarks = content[1]
							 _this.againQuotation(price,remarks)
							 
							 break
						
					}
					
		},
		againQuotation:function(price,remarks){
			
			let data={
				quotationNumber : _quotationNumber,  //	询价单号
				unitPrice : price,
				remarks: remarks
			}
			let url = this.Api.quoteAgain
			uni.showLoading({
				title: '提交中',
				mask: true,
			});
			this.myRequest(data,url,'get').then(res => {
			   uni.hideLoading()
			   if (res.data.status == 0){
				   this.getInquiryInfo()
			   }
			}).catch(err => {
			  wx.showToast({
			    title: err.data.errMsg,
			    icon: 'none'
			  });
			});
		},
		getInquiryInfo: function(){
			let data={
				quotationNumber: _quotationNumber,  //	询价单号
			}
			let url = this.Api.sellerQuotationDetail
			this.myRequest(data,url,'get').then(res => {
			  console.log(res);
			  _this.quotationInfo = res.data.data
			  console.log(_this.quotationInfo)
			}).catch(err => {
			  wx.showToast({
			    title: err.data.errMsg,
			    icon: 'none'
			  });
			});
		}
	}
	
}
